'use client';

import React, { useState } from 'react';
import {
    Box,
    Typography,
    Paper,
    Button,
    Autocomplete,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Fade,
    Snackbar,
    Alert,
    Grid,
    Divider
} from '@mui/material';

// --- Types ---
interface Employee {
    id: string;
    name: string;
    section: string;
    recruitmentType: string;
    designation: string;
    ulbName: string;
    wardName: string;
}

interface MappedRole {
    id: number;
    ulbName: string;
    userType: string;
    role: string;
    level: string;
}

// --- Mock Data ---
const MOCK_EMPLOYEES: Employee[] = [
    { id: 'EMP001', name: 'Ramesh Kumar', section: 'Engineering', recruitmentType: 'Permanent', designation: 'Junior Engineer', ulbName: 'Bengaluru', wardName: 'Ward 1' },
    { id: 'EMP002', name: 'Suresh Patil', section: 'Health', recruitmentType: 'Contract', designation: 'Health Inspector', ulbName: 'Mysuru', wardName: 'Ward 5' },
    { id: '1234', name: 'ABCD', section: 'Administration', recruitmentType: 'Permanent', designation: 'Clerk', ulbName: 'Nagamangala', wardName: 'Ward 10' },
];

const MOCK_ULBS = ['Bengaluru', 'Mysuru', 'Nagamangala', 'Hassan', 'Tumakuru'];
const MOCK_ROLES = ['JE', 'AE', 'AEE', 'Health Inspector', 'Revenue Inspector', 'Clerk'];
const MOCK_LEVELS = ['Level 0', 'Level 1', 'Level 2', 'Level 3'];
const MOCK_USER_TYPES = ['ULB Employee', 'ULB Head'];

const THEME_BLUE = '#002147';
const THEME_RED = '#ff0000';
const THEME_GREEN = '#00c853';

const MapUser: React.FC = () => {
    // Search State
    const [searchQuery, setSearchQuery] = useState<Employee | null>(null);
    const [employeeDetails, setEmployeeDetails] = useState<Employee | null>(null);

    // Form State
    const [preferredUlb, setPreferredUlb] = useState<string | null>(null);
    const [userType, setUserType] = useState<string | null>(null);
    const [role, setRole] = useState<string | null>(null);
    const [level, setLevel] = useState<string | null>(null);

    // Grid State
    const [mappedRoles, setMappedRoles] = useState<MappedRole[]>([
        { id: 1, ulbName: 'Nelamangala', userType: '-', role: 'ULB Head', level: 'Level 0' } // Sample existing data
    ]);

    const [notification, setNotification] = useState<{ open: boolean; message: string; type: 'success' | 'error' | 'info' }>({
        open: false,
        message: '',
        type: 'success'
    });

    // --- Search Handler ---
    const handleSearch = () => {
        if (!searchQuery) {
            setNotification({ open: true, message: 'Please select an employee to search', type: 'error' });
            return;
        }
        setEmployeeDetails(searchQuery);
        if (searchQuery.ulbName) {
            setPreferredUlb(searchQuery.ulbName);
        }
    };

    const handleClear = () => {
        setSearchQuery(null);
        setEmployeeDetails(null);
        setPreferredUlb(null);
        setUserType(null);
        setRole(null);
        setLevel(null);
    };

    // --- Add Handler ---
    const handleAdd = () => {
        if (!employeeDetails) {
            setNotification({ open: true, message: 'Please search and select an employee first', type: 'error' });
            return;
        }

        if (!preferredUlb || !userType || !role) {
            setNotification({ open: true, message: 'Please select all mandatory fields (Preferred ULB, User Type, Role)', type: 'error' });
            return;
        }

        if (userType === 'ULB Head') {
            const existingHead = mappedRoles.find(m => m.ulbName === preferredUlb && m.userType === 'ULB Head');
            if (existingHead) {
                setNotification({ open: true, message: `Access Denied: ${preferredUlb} already has a designated ULB Head.`, type: 'error' });
                return;
            }
        }

        const isDuplicate = mappedRoles.some(m => m.ulbName === preferredUlb && m.role === role && m.userType === userType);
        if (isDuplicate) {
            setNotification({ open: true, message: 'This role is already assigned to same employee', type: 'error' });
            return;
        }

        const hasHeadRole = mappedRoles.some(m => m.userType === 'ULB Head' && m.ulbName === preferredUlb);
        const hasEmployeeRole = mappedRoles.some(m => m.userType === 'ULB Employee' && m.ulbName === preferredUlb);

        if ((userType === 'ULB Head' && hasEmployeeRole) || (userType === 'ULB Employee' && hasHeadRole)) {
            setNotification({
                open: true,
                message: `This Role is already assigned to ${employeeDetails.name} in ${preferredUlb} ULB`,
                type: 'error'
            });
            return;
        }

        const newRole: MappedRole = {
            id: Date.now(),
            ulbName: preferredUlb,
            userType,
            role,
            level: level || '-'
        };

        setMappedRoles([...mappedRoles, newRole]);
        setNotification({ open: true, message: 'Role added successfully', type: 'success' });
    };

    const handleRevoke = (id: number) => {
        setMappedRoles(mappedRoles.filter(r => r.id !== id));
        setNotification({ open: true, message: 'Access revoked successfully', type: 'info' });
    };

    const DetailItem = ({ label, value }: { label: string, value: string }) => (
        <Box sx={{ mb: 1 }}>
            <Typography variant="caption" sx={{ color: '#64748b', display: 'block', mb: 0.5, fontWeight: 500 }}>
                {label}
            </Typography>
            <Box sx={{
                bgcolor: '#f1f5f9',
                p: 1,
                borderRadius: 1,
                border: '1px solid #cbd5e1',
                minHeight: '35px',
                display: 'flex',
                alignItems: 'center'
            }}>
                <Typography variant="body2" sx={{ fontWeight: 600, color: '#334155' }}>
                    {value || '-'}
                </Typography>
            </Box>
        </Box>
    );

    return (
        <Fade in={true}>
            <Box sx={{ maxWidth: '100%', mx: 'auto' }}>
                <Paper elevation={3} sx={{ borderRadius: 2, overflow: 'hidden' }}>
                    {/* Header */}
                    <Box sx={{ bgcolor: THEME_BLUE, py: 2, px: 3 }}>
                        <Typography variant="h6" sx={{ color: 'white', fontWeight: 600, textAlign: 'center' }}>
                            Map User - Role and Menu Mapping
                        </Typography>
                    </Box>

                    <Box sx={{ p: 4 }}>
                        {/* Search Section */}
                        <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center', mb: 5, alignItems: 'center' }}>
                            <Autocomplete
                                sx={{ width: 450 }}
                                options={MOCK_EMPLOYEES}
                                getOptionLabel={(option) => `${option.name} (${option.id})`}
                                value={searchQuery}
                                onChange={(_, newValue) => setSearchQuery(newValue)}
                                renderInput={(params) => <TextField {...params} label="Search User / Employee" variant="outlined" size="medium" />}
                            />
                            <Button
                                variant="contained"
                                size="large"
                                onClick={handleSearch}
                                sx={{ bgcolor: THEME_BLUE, textTransform: 'none', px: 4, '&:hover': { bgcolor: '#001a38' } }}
                            >
                                Search
                            </Button>
                            <Button
                                variant="outlined"
                                size="large"
                                onClick={handleClear}
                                sx={{ color: '#64748b', borderColor: '#cbd5e1', textTransform: 'none', px: 4, '&:hover': { borderColor: '#94a3b8', bgcolor: '#f8fafc' } }}
                            >
                                Clear
                            </Button>
                        </Box>

                        <Divider sx={{ mb: 4 }} />

                        {/* Basic Details Section */}
                        <Box sx={{ mb: 5 }}>
                            <Typography variant="h6" sx={{ color: THEME_BLUE, fontWeight: 700, mb: 3 }}>
                                Basic Details
                            </Typography>
                            <Paper variant="outlined" sx={{ p: 3, bgcolor: '#fff', borderRadius: 2, border: '1px solid #e2e8f0' }}>
                                <Grid container spacing={3}>
                                    <Grid item xs={12} md={3}><DetailItem label="Employee ID/Code" value={employeeDetails?.id || ''} /></Grid>
                                    <Grid item xs={12} md={3}><DetailItem label="User Name" value={employeeDetails?.name || ''} /></Grid>
                                    <Grid item xs={12} md={3}><DetailItem label="Ward Name" value={employeeDetails?.wardName || ''} /></Grid>
                                    <Grid item xs={12} md={3}><DetailItem label="Section Name" value={employeeDetails?.section || ''} /></Grid>
                                    <Grid item xs={12} md={3}><DetailItem label="Recruitment Type" value={employeeDetails?.recruitmentType || ''} /></Grid>
                                    <Grid item xs={12} md={3}><DetailItem label="ULB Name" value={employeeDetails?.ulbName || ''} /></Grid>
                                    <Grid item xs={12} md={3}><DetailItem label="Designation" value={employeeDetails?.designation || ''} /></Grid>
                                </Grid>
                            </Paper>
                        </Box>

                        <Divider sx={{ mb: 4 }} />

                        {/* Map Roles Section */}
                        <Box sx={{ mb: 5 }}>
                            <Typography variant="h6" sx={{ color: THEME_BLUE, fontWeight: 700, mb: 3 }}>
                                Map Preferred ULB's
                            </Typography>
                            <Paper variant="outlined" sx={{ p: 3, bgcolor: '#f8fafc', borderRadius: 2, border: '1px solid #e2e8f0' }}>
                                <Grid container spacing={3} alignItems="flex-end">
                                    <Grid item xs={12} md={3}>
                                        <Typography variant="caption" sx={{ fontWeight: 600, color: '#334155', mb: 1, display: 'block' }}>Preferred ULB *</Typography>
                                        <Autocomplete
                                            fullWidth
                                            size="small"
                                            options={MOCK_ULBS}
                                            value={preferredUlb}
                                            onChange={(_, val) => setPreferredUlb(val)}
                                            renderInput={(params) => <TextField {...params} variant="outlined" sx={{ bgcolor: 'white' }} />}
                                        />
                                    </Grid>
                                    <Grid item xs={12} md={3}>
                                        <Typography variant="caption" sx={{ fontWeight: 600, color: '#334155', mb: 1, display: 'block' }}>User Type *</Typography>
                                        <Autocomplete
                                            fullWidth
                                            size="small"
                                            options={MOCK_USER_TYPES}
                                            value={userType}
                                            onChange={(_, val) => setUserType(val)}
                                            renderInput={(params) => <TextField {...params} variant="outlined" sx={{ bgcolor: 'white' }} />}
                                        />
                                    </Grid>
                                    <Grid item xs={12} md={2}>
                                        <Typography variant="caption" sx={{ fontWeight: 600, color: '#334155', mb: 1, display: 'block' }}>Role *</Typography>
                                        <Autocomplete
                                            fullWidth
                                            size="small"
                                            options={MOCK_ROLES}
                                            value={role}
                                            onChange={(_, val) => setRole(val)}
                                            renderInput={(params) => <TextField {...params} variant="outlined" sx={{ bgcolor: 'white' }} />}
                                        />
                                    </Grid>
                                    <Grid item xs={12} md={2}>
                                        <Typography variant="caption" sx={{ fontWeight: 600, color: '#334155', mb: 1, display: 'block' }}>Level</Typography>
                                        <Autocomplete
                                            fullWidth
                                            size="small"
                                            options={MOCK_LEVELS}
                                            value={level}
                                            onChange={(_, val) => setLevel(val)}
                                            renderInput={(params) => <TextField {...params} variant="outlined" sx={{ bgcolor: 'white' }} />}
                                        />
                                    </Grid>
                                    <Grid item xs={12} md={2}>
                                        <Button
                                            fullWidth
                                            variant="contained"
                                            onClick={handleAdd}
                                            sx={{
                                                bgcolor: THEME_BLUE,
                                                textTransform: 'none',
                                                fontWeight: 600,
                                                height: '40px',
                                                '&:hover': { bgcolor: '#001a38' }
                                            }}
                                        >
                                            Add ULB and Role
                                        </Button>
                                    </Grid>
                                </Grid>
                            </Paper>
                        </Box>

                        {/* Table */}
                        <TableContainer component={Paper} elevation={0} sx={{ border: '1px solid #e2e8f0', borderRadius: 2 }}>
                            <Table>
                                <TableHead>
                                    <TableRow sx={{ bgcolor: THEME_BLUE }}>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, width: '60px', textAlign: 'center' }}>Sl No</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700 }}>ULB Name</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700 }}>User Type</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700 }}>Role</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700 }}>Level</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, textAlign: 'center', width: '250px' }}>Action</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {mappedRoles.length === 0 ? (
                                        <TableRow>
                                            <TableCell colSpan={6} align="center" sx={{ py: 6, color: '#64748b' }}>No roles mapped</TableCell>
                                        </TableRow>
                                    ) : (
                                        mappedRoles.map((row, index) => (
                                            <TableRow key={row.id} sx={{ '&:nth-of-type(odd)': { bgcolor: '#f8fafc' }, '&:hover': { bgcolor: '#f1f5f9' } }}>
                                                <TableCell align="center" sx={{ fontWeight: 500, color: '#64748b' }}>{index + 1}</TableCell>
                                                <TableCell sx={{ fontWeight: 600, color: '#1e293b' }}>{row.ulbName}</TableCell>
                                                <TableCell>{row.userType}</TableCell>
                                                <TableCell>{row.role}</TableCell>
                                                <TableCell>{row.level}</TableCell>
                                                <TableCell align="center">
                                                    <Box sx={{ display: 'flex', justifyContent: 'center', gap: 1 }}>
                                                        <Button
                                                            variant="contained"
                                                            size="small"
                                                            sx={{
                                                                bgcolor: THEME_GREEN,
                                                                textTransform: 'none',
                                                                fontWeight: 600,
                                                                boxShadow: 'none',
                                                                '&:hover': { bgcolor: '#00a344' }
                                                            }}
                                                        >
                                                            Map Menus
                                                        </Button>
                                                        <Button
                                                            variant="contained"
                                                            size="small"
                                                            onClick={() => handleRevoke(row.id)}
                                                            sx={{
                                                                bgcolor: THEME_RED,
                                                                textTransform: 'none',
                                                                fontWeight: 600,
                                                                boxShadow: 'none',
                                                                '&:hover': { bgcolor: '#d32f2f' }
                                                            }}
                                                        >
                                                            Revoke
                                                        </Button>
                                                    </Box>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    )}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Box>
                </Paper>

                {/* Notification */}
                <Snackbar
                    open={notification.open}
                    autoHideDuration={4000}
                    onClose={() => setNotification({ ...notification, open: false })}
                    anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                >
                    <Alert severity={notification.type} onClose={() => setNotification({ ...notification, open: false })} variant="filled">
                        {notification.message}
                    </Alert>
                </Snackbar>
            </Box>
        </Fade>
    );
};

export default MapUser;
