'use client';

import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,
  IconButton,
  Menu,
  MenuItem,
  Fade,
  Button
} from '@mui/material';
import {
  AccountCircle,
  Notifications,
  ExitToApp,
  People,
  KeyboardArrowDown
} from '@mui/icons-material';
import UserManagement from '../admin/UserManagement';
import MapUlbToDistrict from '../admin/MapUlbToDistrict';
import MapSuperAdmin from '../admin/MapSuperAdmin';
import MapLevelsToRoles from '../admin/MapLevelsToRoles';
import MapUser from '../admin/MapUser';

interface DashboardProps {
  onLogout: () => void;
}

const Dashboard: React.FC<DashboardProps> = ({ onLogout }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [anchorElAdmin, setAnchorElAdmin] = useState<null | HTMLElement>(null);
  const [currentView, setCurrentView] = useState<'employees' | 'map-ulb' | 'map-super-admin' | 'map-levels' | 'map-user'>('employees');

  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleAdminMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElAdmin(event.currentTarget);
  };

  const handleAdminClose = () => {
    setAnchorElAdmin(null);
  };

  const handleAdminNavigation = (view: 'employees' | 'map-ulb' | 'map-super-admin' | 'map-levels' | 'map-user') => {
    setCurrentView(view);
    handleAdminClose();
  };

  const handleLogout = () => {
    handleClose();
    onLogout();
  };

  return (
    <Box sx={{ minHeight: '100vh', bgcolor: '#f1f5f9', display: 'flex', flexDirection: 'column' }}>

      {/* Top Accessibility Bar */}
      <Box sx={{ bgcolor: '#1e293b', color: '#cbd5e1', py: 0.5, px: 3, display: 'flex', justifyContent: 'flex-end', borderBottom: '1px solid #334155' }}>
        <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
          <Typography variant="caption" sx={{ fontSize: '11px', mr: 2 }}>Skip to Main Content</Typography>
          {['A', 'A', '-', 'A', '+', 'En'].map((text, index) => (
            <Box
              key={index}
              component="button"
              sx={{
                border: '1px solid #475569',
                bgcolor: 'transparent',
                color: 'inherit',
                px: 1,
                fontSize: '10px',
                borderRadius: 1,
                cursor: 'pointer',
                '&:hover': { bgcolor: '#334155', color: 'white' },
                transition: 'all 0.2s'
              }}
            >
              {text}
            </Box>
          ))}
        </Box>
      </Box>

      {/* Main Header */}
      <Box sx={{ bgcolor: 'white', py: 2.5, borderBottom: '1px solid #e2e8f0', boxShadow: '0 4px 6px -1px rgba(0,0,0,0.05)', position: 'relative', zIndex: 10 }}>
        <Container maxWidth="xl">
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
              <Box
                sx={{
                  width: 85,
                  height: 85,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  p: 0.7,
                  borderRadius: 2,
                  border: '1px solid #e2e8f0',
                  boxShadow: '0 4px 6px rgba(0,0,0,0.05)'
                }}
              >
                <img src="/images/karnataka_emblem.png" alt="Karnataka Government Logo" style={{ width: '100%', height: '100%', objectFit: 'contain' }} />
              </Box>
              <Box>
                <Typography variant="h4" sx={{ fontWeight: 800, color: '#0f172a', lineHeight: 1.2, letterSpacing: '-0.5px' }}>
                  Directorate of Municipal Administration
                </Typography>
                <Typography variant="subtitle1" sx={{ color: '#002147', fontWeight: 700, letterSpacing: '1.5px', textTransform: 'uppercase', mt: 0.7 }}>
                  Government of Karnataka
                </Typography>
              </Box>
            </Box>

            <Box sx={{ display: { xs: 'none', md: 'block' } }}>
              <Paper elevation={0} sx={{ p: 1.5, borderRadius: 2, border: '1px solid #e2e8f0' }}>
                <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
                  <Typography variant="caption" sx={{ color: '#64748b', mb: 0.5, fontWeight: 600 }}>Helpline Number</Typography>
                  <Typography variant="h6" sx={{ fontWeight: 700, color: '#002147' }}>1800-425-1111</Typography>
                </Box>
              </Paper>
            </Box>
          </Box>
        </Container>
      </Box>

      {/* Navigation Bar */}
      <Box sx={{
        bgcolor: '#002147',
        py: 1.2,
        px: 3,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        background: 'linear-gradient(to right, #002147, #0f172a)'
      }}>
        <Container maxWidth="xl" sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Button
              endIcon={<KeyboardArrowDown />}
              onClick={handleAdminMenu}
              sx={{
                color: ['employees', 'map-ulb', 'map-super-admin', 'map-levels', 'map-user'].includes(currentView) ? 'white' : '#94a3b8',
                bgcolor: ['employees', 'map-ulb', 'map-super-admin', 'map-levels', 'map-user'].includes(currentView) ? 'rgba(255,255,255,0.1)' : 'transparent',
                '&:hover': { bgcolor: 'rgba(255,255,255,0.2)', color: 'white' },
                fontWeight: 700,
                textTransform: 'none',
                fontSize: '1rem',
                borderRadius: 2,
                px: 2,
                py: 1
              }}
            >
              Admin
            </Button>
            <Menu
              anchorEl={anchorElAdmin}
              open={Boolean(anchorElAdmin)}
              onClose={handleAdminClose}
              MenuListProps={{
                'aria-labelledby': 'basic-button',
              }}
              PaperProps={{
                elevation: 3,
                sx: {
                  marginTop: '10px',
                  overflow: 'visible',
                  filter: 'drop-shadow(0px 4px 12px rgba(0,0,0,0.2))',
                  bgcolor: 'background.paper',
                  borderRadius: 2,
                  minWidth: 220
                }
              }}
            >
              <MenuItem 
                onClick={() => handleAdminNavigation('employees')}
                sx={{ 
                  py: 1.2,
                  '&:hover': { bgcolor: '#f1f5f9' }
                }}
              >
                Employee / User Details
              </MenuItem>
              <MenuItem 
                onClick={() => handleAdminNavigation('map-ulb')}
                sx={{ 
                  py: 1.2,
                  '&:hover': { bgcolor: '#f1f5f9' }
                }}
              >
                Map ULB to District
              </MenuItem>
              <MenuItem 
                onClick={() => handleAdminNavigation('map-super-admin')}
                sx={{ 
                  py: 1.2,
                  '&:hover': { bgcolor: '#f1f5f9' }
                }}
              >
                Map Super Admin
              </MenuItem>
              <MenuItem 
                onClick={() => handleAdminNavigation('map-levels')}
                sx={{ 
                  py: 1.2,
                  '&:hover': { bgcolor: '#f1f5f9' }
                }}
              >
                Map Levels to Roles
              </MenuItem>
              <MenuItem 
                onClick={() => handleAdminNavigation('map-user')}
                sx={{ 
                  py: 1.2,
                  '&:hover': { bgcolor: '#f1f5f9' }
                }}
              >
                Map User
              </MenuItem>
            </Menu>
          </Box>

          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', color: 'white' }}>
            <IconButton 
              size="medium" 
              sx={{ 
                color: 'white', 
                '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' },
                borderRadius: 2
              }}
            >
              <Notifications />
            </IconButton>
            <Box>
              <IconButton
                size="medium"
                onClick={handleMenu}
                sx={{
                  color: 'white',
                  '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' },
                  border: '1px solid rgba(255,255,255,0.2)',
                  borderRadius: 2,
                  p: 1
                }}
              >
                <AccountCircle sx={{ fontSize: '1.8rem' }} />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                PaperProps={{
                  elevation: 3,
                  sx: {
                    overflow: 'visible',
                    filter: 'drop-shadow(0px 4px 12px rgba(0,0,0,0.2))',
                    mt: 1.5,
                    borderRadius: 2,
                    minWidth: 180,
                    '& .MuiAvatar-root': {
                      width: 32,
                      height: 32,
                      ml: -0.5,
                      mr: 1,
                    },
                    '&:before': {
                      content: '""',
                      display: 'block',
                      position: 'absolute',
                      top: 0,
                      right: 14,
                      width: 10,
                      height: 10,
                      bgcolor: 'background.paper',
                      transform: 'translateY(-50%) rotate(45deg)',
                      zIndex: 0,
                    },
                  },
                }}
              >
                <MenuItem 
                  onClick={handleClose}
                  sx={{ py: 1.2 }}
                >
                  Profile
                </MenuItem>
                <MenuItem 
                  onClick={handleLogout} 
                  sx={{ 
                    color: 'error.main', 
                    py: 1.2,
                    '&:hover': { bgcolor: '#fef2f2' }
                  }}
                >
                  <ExitToApp sx={{ mr: 1 }} />
                  Logout
                </MenuItem>
              </Menu>
            </Box>
          </Box>
        </Container>
      </Box>

      {/* Main Content */}
      <Container maxWidth="xl" sx={{ mt: 4, mb: 4, flexGrow: 1 }}>
        <Fade in timeout={800} key={currentView}>
          <Box>
            {currentView === 'employees' ? <UserManagement /> :
              currentView === 'map-ulb' ? <MapUlbToDistrict /> :
                currentView === 'map-super-admin' ? <MapSuperAdmin /> :
                  currentView === 'map-levels' ? <MapLevelsToRoles /> :
                    <MapUser />}
          </Box>
        </Fade>
      </Container>
    </Box>
  );
};

export default Dashboard;